import request from "../../helpers/request";
import auth from "../../api/auth";
import blog from "../../api/blog";
window.request = request
window.auth = auth
window.blog = blog


export default {

    data(){

        return {
            msg:'welcome to vue'
        }
    },
    methods:{

        onClick1(){
            this.$message({
                message: '',
                type: 'success'
            });
        },
        onClick2() {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        }
    }


}